import React from 'react';

const List = ({ people, onDelete }) => {
    return (
        <>
            {people.map((person) => {
                return (
                    <article className='person' key={person.id}>
                        <img src={person.image} alt={person.name} />
                        <div>
                            <h4>{person.name}</h4>
                            <p>{person.age} years</p>
                        </div>
                        <button onClick={() => onDelete(person.id)}>X</button>
                    </article>
                );
            })}
        </>
    );
};

export default List;
